const columnsWithdrawal = [
  {
    title: 'Birim',
    dataIndex: 'Asset',
    key: 'Asset',
    width: '10%',
    scopedSlots: {
      customRender: 'Asset',
    },
  },
  {
    title: 'Durum',
    dataIndex: 'Status',
    key: 'Status',
    width: '15%',
    scopedSlots: {
      customRender: 'Status',
    },
  },
  {
    title: 'Miktar',
    dataIndex: 'Amount',
    key: 'Amount',
    width: '10%',
    scopedSlots: {
      customRender: 'Amount',
    },
  },
  {
    title: 'Adres',
    dataIndex: 'Address',
    key: 'Address',
    class: 'date',

    scopedSlots: {
      customRender: 'Address',
    },
  },
  {
    title: 'Tarih',
    dataIndex: 'CreatedOn',
    key: 'CreatedOn',

    width: '20%',
    scopedSlots: {
      customRender: 'CreatedOn',
    },
  },
  {
    title: '',
    dataIndex: '',
    key: '',
    class: '',

    scopedSlots: {
      customRender: '',
    },
  },
]
const columnsFiatWithdrawal = [
  {
    title: 'Tarih',
    dataIndex: 'DateTime',
    key: 'DateTime',

    width: '15%',
    scopedSlots: {
      customRender: 'DateTime',
    },
  },
  {
    title: 'Birim',
    dataIndex: 'Asset',
    key: 'Asset',
    width: '18%',

    scopedSlots: {
      customRender: 'Asset',
    },
  },
  {
    title: 'Miktar',
    dataIndex: 'Amount',
    key: 'Amount',
    width: '18%',

    scopedSlots: {
      customRender: 'Amount',
    },
  },
  {
    title: 'Hesap NumarasÄ±',
    dataIndex: 'To',
    key: 'To',
    width: '10%',

    scopedSlots: {
      customRender: 'To',
    },
  },
  {
    title: 'Durum',
    dataIndex: 'Status',
    key: 'Status',
    width: '10%',

    scopedSlots: {
      customRender: 'Status',
    },
  },
]
const columnsTranfer = [
  {
    title: 'Nereden',
    dataIndex: 'From',
    key: 'From',

    scopedSlots: {
      customRender: 'From',
    },
  },
  {
    title: 'Nereye',
    dataIndex: 'To',
    key: 'To',

    scopedSlots: {
      customRender: 'To',
    },
  },
  {
    title: 'Miktar',
    dataIndex: 'Amount',
    key: 'Amount',

    scopedSlots: {
      customRender: 'Amount',
    },
  },
  {
    title: 'Birim',
    dataIndex: 'Asset',
    key: 'Asset',

    scopedSlots: {
      customRender: 'Asset',
    },
  },
  {
    title: 'Tarih',
    dataIndex: 'CreatedOn',
    key: 'CreatedOn',
    class: 'date',
    scopedSlots: {
      customRender: 'CreatedOn',
    },
  },
]
export default {
  name: 'Price',
  data() {
    return {
      dataRelevant: [],
      dataFiatRelevant: [],
      dataTransfer: [],
      originalData: [],
      pagination: {
        pageSize: 15,
        total: 200,
        current: 1,
      },
      paginationRelevant: {
        pageSize: 10,
        total: 10,
        current: 1,
      },
      deneme: {},

      fromToDropdownContent: [],
      unitDromDownContent: [],
      transferListSearch: {
        fromValue: 'ALL',
        toValue: 'ALL',
        unitValue: '',
      },

      alert: false,
      searchInput: null,
      loading: false,
      totalSpotBalanceInBtc: 0,
      columnsTranfer,
      columnsWithdrawal,
      isMobile: false,
      activeKey: ['1'],
      columnsFiatWithdrawal,
      showPagination: false,
    }
  },
  mounted() {
    this.isMobile = window.innerWidth < 1000

    window.addEventListener('resize', () => {
      this.isMobile = window.innerWidth < 1000
    })

    this.getDepositHistory()
    this.getDropdownContent()
    this.getTransferHistory()
    this.getFiatDepositHistory()
  },

  methods: {
    handleTableChange(pagination) {
      this.getTransferHistory(
        this.transferListSearch.unitValue,
        this.transferListSearch.fromValue,
        this.transferListSearch.toValue,
        pagination.current - 1,
        pagination.pageSize
      )
    },
    handleTableChange_mobile(current, size) {
      this.getTransferHistory(
        this.transferListSearch.unitValue,
        this.transferListSearch.fromValue,
        this.transferListSearch.toValue,
        current - 1,
        size
      )
    },
    customExpandIcon(props) {
      if (props.expanded) {
        return (
          <a
            style={{ color: 'black' }}
            onClick={(e) => {
              props.onExpand(props.record, e)
            }}
          >
            <Icon type="minus" />
          </a>
        )
      } else {
        return (
          <a
            style={{ color: 'black' }}
            onClick={(e) => {
              props.onExpand(props.record, e)
            }}
          >
            <Icon type="plus" />
          </a>
        )
      }
    },
    searchTransfer(isSearch) {
      // if (this.transferListSearch.toValue) {
      //   this.alert = true
      //   setTimeout(() => {
      //     this.alert = false
      //   }, 3000)
      // } else {

      // }
      if (isSearch) {
        
        
        this.getTransferHistory(
          this.transferListSearch.unitValue,
          this.transferListSearch.fromValue,
          this.transferListSearch.toValue,
          this.pagination.current - 1,
          this.pagination.pageSize
        )
      } else {
        this.transferListSearch = {
          fromValue: 'ALL',
          toValue: 'ALL',
          unitValue: '',
        }
        this.getTransferHistory()
      }
    },

    searchCoin() {
      let filteredData = this.data.filter((element) =>
        element.asset.toLowerCase().includes(this.searchInput.toLowerCase())
      )
      this.data = this.searchInput === '' ? this.originalData : filteredData
    },
    handleChangeFrom(e) {
      this.transferListSearch.fromValue = e
    },

    handleChangeTo(e) {
      this.transferListSearch.toValue = e
    },
    handleChangeUnit(e) {
      this.transferListSearch.unitValue = e
    },
    handleSwitch() {
      ;[this.transferListSearch.fromValue, this.transferListSearch.toValue] = [
        this.transferListSearch.toValue,
        this.transferListSearch.fromValue,
      ]
    },
    getReleventHistory(e) {
      switch (e) {
        case 'depozito':
          this.showPagination = false
          this.getDepositHistory()
          break
        case 'withdraw':
          this.showPagination = true
          this.getWithdrawHistory(
            this.paginationRelevant.current - 1,
            this.paginationRelevant.pageSize
          )
          break

        default:
          break
      }
    },
    getReleventFiatHistory(e) {
      
      switch (e) {
        case 'fiatwithdraw':
          this.getFiatWithdrawHistory()

          break
        case 'fiatdeposit':
          this.getFiatDepositHistory(
            this.paginationRelevant.current - 1,
            this.paginationRelevant.pageSize
          )
          break
        default:
          break
      }
    },
    async getDepositHistory() {
      try {
        let authHeaders = {
          Authorization: 'Bearer ' + this.$cookies.get('Token'),
        }

        const response = await this.$axios.get(
          this.$config.FINANCE_API_URL + '/GetDepositHistory',
          {
            headers: authHeaders,
          }
        )
        
        if (response.data) {
          this.loading = false
          this.dataRelevant = response.data.DepositHistory
          
          // this.originalData = response.data.UserSpotBalance
          // this.totalSpotBalanceInBtc = response.data.TotalSpotBalanceInBtc
        }
      } catch (e) {
        
      }
    },
    async getWithdrawHistory(offset = 0, RowSize = 10) {
      try {
        let authHeaders = {
          Authorization: 'Bearer ' + this.$cookies.get('Token'),
        }

        const response = await this.$axios.get(
          this.$config.FINANCE_API_URL +
            `/GetWithdrawHistory?Offset=${offset}&RowSize=${RowSize}`,
          {
            headers: authHeaders,
          }
        )
        
        if (response.data) {
          this.paginationRelevant.total = response.data.Max
          this.paginationRelevant.current = offset + 1
          
          this.dataRelevant = response.data.WithdrawRecordList
        }
      } catch (e) {
        
      }
    },
    async getFiatWithdrawHistory() {
      try {
        let authHeaders = {
          Authorization: 'Bearer ' + this.$cookies.get('Token'),
        }

        const response = await this.$axios.get(
          this.$config.FINANCE_API_URL + `/GetFiatWalletWithdrawHistory`,
          {
            headers: authHeaders,
          }
        )
        
        if (response.data) {
          
          this.dataFiatRelevant = response.data.FiatWalletWithdraw
        }
      } catch (e) {
        
      }
    },
    async getFiatDepositHistory(offset = 0, RowSize = 10) {
      try {
        let authHeaders = {
          Authorization: 'Bearer ' + this.$cookies.get('Token'),
        }

        const response = await this.$axios.get(
          this.$config.FINANCE_API_URL +
            `/GetUserFiatWalletDepositPage?Offset=${offset}&RowSize=${RowSize}`,
          {
            headers: authHeaders,
          }
        )
        
        if (response.data) {
          
          this.paginationRelevant.total = response.data.Max
          this.paginationRelevant.current = offset + 1
          
          this.dataFiatRelevant = response.data.FiatDepositList
        }
      } catch (e) {
        
      }
    },
    async getTransferHistory(
      asset = '',
      from = 'ALL',
      to = 'ALL',
      offset = 0,
      RowSize = 15
    ) {
      try {
        let authHeaders = {
          Authorization: 'Bearer ' + this.$cookies.get('Token'),
        }

        const response = await this.$axios.get(
          this.$config.FINANCE_API_URL +
            `/GetTransferHistory?Asset=${asset}&From=${from}&To=${to}&Offset=${offset}&RowSize=${RowSize}`,
          {
            headers: authHeaders,
          }
        )

        if (response.data) {
          // TODO: response.data['Message']
          this.dataTransfer = response.data.TransferHistory
          this.pagination.total = response.data.Max
          this.pagination.current = offset + 1

          
          
        }
      } catch (e) {
        
      }
    },
    async getDropdownContent() {
      try {
        let authHeaders = {
          Authorization: 'Bearer ' + this.$cookies.get('Token'),
        }

        const response = await this.$axios.get(
          this.$config.FINANCE_API_URL + `/GetFilters`,
          {
            headers: authHeaders,
          }
        )

        if (response.data) {
          // TODO: response.data['Message']
          this.loading = false

          this.fromToDropdownContent = response.data.TransferList.map(
            ({ Name }) => Name
          )
          this.unitDromDownContent = response.data.AssetList.map(
            ({ Name }) => Name
          )

          
          
          // this.originalData = response.data.UserSpotBalance
          // this.totalSpotBalanceInBtc = response.data.TotalSpotBalanceInBtc
        }
      } catch (e) {
        
      }
    },
  },
}
